import React from "react";
import banner from "../Images/Images/indian-wedding-background-af1hetugsl7pkndq.jpg";
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import Auth_User from "../../authentication/Auth_User";
import AliceCarousel from "react-alice-carousel";
import "react-alice-carousel/lib/alice-carousel.css";
import "./Dashboard.css";

const Dashboard = () => {
  const { http, user, token, logout } = Auth_User();

  const banner1 = {
    backgroundImage: `url(${banner})`,
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    width: "100%",
    height: "500px",
    marginTop: "-120px",
  };

  const [Cast, SetCast] = useState([]);

  useEffect(() => {
    http.get(`/member/imformation/for_fillter`).then((res) => {
      SetCast(res.data.cast);
    });
  }, []);

  return (
    <>
      <div className="body1">
        <section id="" class="clearfix" style={{ marginTop: "00px" }}>
          <div class="dashboard_main clearfix">
            <div class="container" style={banner1}>
              <div class="row">
                <div class="dashboard clearfix col-lg-12 col-sm-12 col-md-12">
                  <div class="col-sm-7 col-lg-7 dashboard_left clearfix">
                    <div
                      class="center_left_inner clearfix_1"
                      style={{ position: "relative" }}
                    >
                      <h1
                        style={{
                          marginTop: "190px",
                          fontFamily: "'Pacifico', cursive",
                        }}
                      >
                        Dashboard
                      </h1>

                      <h3>
                        <Link to="/" style={{ color: "white" }}>
                          Home-Dashboard
                        </Link>
                        <br />
                        <Link to={"/profile"} className="btn btn-success aa">
                          Profile
                        </Link>
                      </h3>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section>
          <div className="container">
            <div className="main-body">
              {/* Breadcrumb */}
              <nav aria-label="breadcrumb" className="main-breadcrumb">
                <ol className="breadcrumb">
                  <li className="breadcrumb-item">
                    <a href="index.html">Home</a>
                  </li>
                  <li className="breadcrumb-item">
                    <a href="javascript:void(0)">User</a>
                  </li>
                  <li className="breadcrumb-item active" aria-current="page">
                    User Profile
                  </li>
                </ol>
              </nav>
              {/* /Breadcrumb */}
              <div className="row gutters-sm">
                <div className="col-md-4 mb-3">
                  <div className="card">
                    <div className="card-body">
                      <div className="d-flex flex-column align-items-center text-center">
                        <img
                          src="https://bootdey.com/img/Content/avatar/avatar7.png"
                          alt="Admin"
                          className="rounded-circle"
                          width={150}
                        />
                        <div className=" card mt-3">
                          <p
                            className="text-danger mb-1 "
                            style={{ fontSize: "30px" }}
                          >
                            Upload Photo
                          </p>
                          {/* <p className="text-muted font-size-sm">Bay Area, San Francisco, CA</p> */}
                          <button className="btn btn-primary">
                            Change Profile Picture
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col" style={{ marginTop: "20px" }}>
                    <div className="card bg-danger mt-3">
                      <p className="btn btn-warning "> My Profile</p>
                      <ul className="list-group list-group-flush">
                        <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                          <Link to="/">Edit Profile</Link>
                        </li>
                        <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                          <Link to="/">Manage Photos</Link>
                        </li>
                        <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                          <Link to="/">
                            Shortlist Profile
                          </Link>
                        </li>
                        <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                          <Link to="/">
                            I Visited Profile
                          </Link>
                        </li>
                        <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                          <Link to="/">
                            Who Viewed Your Profile
                          </Link>
                        </li>
                        <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                          <Link to="/">Lock My Profile</Link>
                        </li>
                        <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                          <Link to="/">Active Plans</Link>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
                <div className="col-md-8" >
                  <div className="card mb-3">
                    <div className="card-body w-100" >
                      <div className="row">
                        <div className="col-sm-3">
                          <h6 className="mb-0">Full Name</h6>
                        </div>
                        <div className="col-sm-9 text-secondary">
                          Kenneth Valdez
                        </div>
                      </div>
                      <hr />
                      <div className="row">
                        <div className="col-sm-3">
                          <h6 className="mb-0">Email</h6>
                        </div>
                        <div className="col-sm-9 text-secondary">
                          fip@jukmuh.al
                        </div>
                      </div>
                      <hr />
                      <div className="row">
                        <div className="col-sm-3">
                          <h6 className="mb-0">Phone</h6>
                        </div>
                        <div className="col-sm-9 text-secondary">
                          (239) 816-9029
                        </div>
                      </div>
                      <hr />
                      <div className="row">
                        <div className="col-sm-3">
                          <h6 className="mb-0">Mobile</h6>
                        </div>
                        <div className="col-sm-9 text-secondary">
                          (320) 380-4539
                        </div>
                      </div>
                      <hr />
                      <div className="row">
                        <div className="col-sm-3">
                          <h6 className="mb-0">Address</h6>
                        </div>
                        <div className="col-sm-9 text-secondary">
                          Bay Area, San Francisco, CA
                        </div>
                      </div>
                      <hr />
                      <div className="row">
                        <div className="col-sm-12">
                          <a
                            className="btn btn-info "
                            target="__blank"
                            href="https://www.bootdey.com/snippets/view/profile-edit-data-and-skills"
                          >
                            Edit
                          </a>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="row gutters-sm">
                    <div className="col-sm-6 mb-3">
                      <div className="card h-100">
                        <div className="card-body">
                          <h6 className="d-flex align-items-center mb-3">
                            <i className="material-icons text-info mr-2">
                              assignment
                            </i>
                            Project Status
                          </h6>
                          <small>Web Design</small>
                          <div className="progress mb-3" style={{ height: 5 }}>
                            <div
                              className="progress-bar bg-primary"
                              role="progressbar"
                              style={{ width: "80%" }}
                              aria-valuenow={80}
                              aria-valuemin={0}
                              aria-valuemax={100}
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="col-sm-6 mb-3">
                      <div className="card h-100">
                        <div className="card-body">
                          <h6 className="d-flex align-items-center mb-3">
                            <i className="material-icons text-info mr-2">
                              assignment
                            </i>
                            Project Status
                          </h6>
                          <small>Web Design</small>
                          <div className="progress mb-3" style={{ height: 5 }}>
                            <div
                              className="progress-bar bg-primary"
                              role="progressbar"
                              style={{ width: "80%" }}
                              aria-valuenow={80}
                              aria-valuemin={0}
                              aria-valuemax={100}
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
};

export default Dashboard;
